.repo-card-div {
    color: #586069;
    background-color: #fafbfc;
    border: 1px solid #d3d3d3;
    border-radius: 10px;
    box-shadow: 0px 10px 30px -15px #00000033;
    padding: 2rem;
    cursor: pointer;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
}
  
.repo-card-div:hover {
    background-color: #645beb;
    color: #ffffff;
}
  
.repo-card-div:hover p {
    color: #ffffff;
}

.repo-card-div:hover .repo-star-svg,
.repo-card-div:hover .repo-svg {
    fill: #ffffff;
}
  
.repo-stats {
    display: flex;
    -webkit-box-pack: justify;
    justify-content: space-between;
    font-size: 13px;
    color: #6a737d;
}
  
.repo-left-stat {
    -webkit-box-flex: 1;
    flex-grow: 1;
    display: flex;
}
  
.language-color {
    width: 10px;
    height: 10px;
    background-color: #55198b;
    margin-right: 0.25rem;
    border-radius: 100%;
}
  
.repo-left-stat span {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    margin-right: 0.75rem;
}
  
.repo-name-div {
    display: flex;
    align-items: center;
}
  
.repo-svg {
    margin-right: 0.5rem;
    min-width: 16px;
}
  
.repo-name {
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #24292e;
    margin-bottom: 0.75rem;
    font-size: 25px;
    font-weight: 700;
    letter-spacing: -0.5px;
    overflow: hidden;
    margin: 0px;
    height: 30px;
}
  
.repo-star-svg {
    margin-right: 0.3rem;
}
  
.repo-description {
    overflow: hidden;
    margin-top: 10px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}
  
.repo-element {
    margin-bottom: 0;
}
  
.dark-card-mode {
    background-color: #171c28;
    color: #ffffff;
}
  
.dark-card-mode p {
    color: #ffffff;
}
  
.dark-card-mode .repo-svg {
    fill: #ffffff;
}
  
.dark-card-mode:hover {
    background-color: #55198b !important;
}
